* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul, ol {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
  display: flex;
  margin: 15px 0;
  padding: 0;
  font-size: 1.2rem;
  min-height: 1.8rem;
  display:flex;
  align-items:center;
}
img {
  display: block;
}
.flag-icon{
  height: auto;
  width: 50px;
  margin-right: 20px;
  
}
.flex{
  display:flex;
  align-items: center;
}
.big-flag{
  height: 40px;
  width: auto;
  margin-right: 20px;
}
h1 {
  font-size: 2.2rem;
}
b {
  margin-right: 10px;
}